        contacts: {
            list: this.get(PATHS.contacts),
            update: this.put(`${PATHS.contacts}/:contactId`),
            delete: this.delete(`${PATHS.contacts}/:contactId`),
        },

        conversations: {
            /** @member {ApiGetMethod<{ next?: string }, { next?: string, assign: string, status: string, sort: string, searchValue: string }>} */
            list: this.get(PATHS.conversations),
            retrieve: this.get(`${PATHS.conversations}/:contactId`),
            /** @member {ApiPutMethod<{ contactId: string }>} */
            read: this.put(`${PATHS.conversations}/:contactId/read`),
            /** @member {ApiPutMethod<{ contactId: string, userId: string }>} */
            assign: this.put(`${PATHS.conversations}/:contactId/assign/:userId`),
            /** @member {ApiPutMethod<{ contactId: string }>} */
            assignMe: this.put(`${PATHS.conversations}/:contactId/assign/me`),
            /** @member {ApiPutMethod<{ contactId: string, status: 'open'|'close' }>} */
            changeStatus: this.put(`${PATHS.conversations}/:contactId/status/:status`),
            /** @member {ApiPutMethod<{ contactId: string }>} */
            block: this.put(`${PATHS.conversations}/:contactId/block`),
            uploadAttachment: async ({ payload, contactId }) => {
                const form = new FormData();
                form.append('file', payload);

                return this.apiCall('POST', `${PATHS.conversations}/:contactId/uploads/attachment`, {
                    contactId,
                    payload: form,
                    query: { field: 'file' },
                });
            },
            sendAttachment: async ({ contactId, payload }) => {
                const { file, ...rest } = payload;
                const { url, filename, mime, size } = await this.resources.conversations.uploadAttachment({
                    contactId,
                    payload: file,
                });

                return this.resources.messages.create({
                    contactId,
                    payload: {
                        type: mime === 'image/png' || mime === 'image/jpeg' ? 'image' : 'file',
                        mediaUrl: url,
                        fileName: filename,
                        fileSize: size,
                        destination: 'client',
                        ...rest,
                    },
                });
            },
            /** @member {ApiPutMethod<{ contactId: string }, { text: string }>} */
            sendMessage: ({ contactId, payload }) => {
                return this.apiCall('POST', PATHS.messages, {
                    contactId,
                    payload: { type: 'text', destination: 'client', ...payload },
                });
            },
            messages: this.get(PATHS.messages),
            /** @member {ApiGetMethod<{}, { status: string }>} */
            statistics: this.get(`${PATHS.conversations}/statistics`),
            unread: this.get(`${PATHS.conversations}/unread`),
        },

