import { createMiddleware } from 'redux-beacon';
import GoogleTagManager from '@redux-beacon/google-tag-manager';
import { omit } from 'lodash';
import { api, checkout } from '../actions';
import sift from 'sift';

const eventsMap = {
    [api.auth.login.successType]: () => ({
        event: 'login',
        method: 'email',
    }),
    [api.users.create.successType]: () => ({
        event: 'sign_up',
        method: 'email',
    }),
    [api.users.create.failureType]: (action) => ({
        event: 'Sign up error',
        error: omit(action.error, ['stack', 'json']),
    }),
    [api.websites.update.successType]: (action, prevState) => {
        const website = prevState.websites.find(sift({ _id: action.websiteId }));
        if (!website?.enabled && action.payload.enabled) {
            return {
                event: 'website_published',
            };
        }
    },
    [api.listenSubscriptionCreated.type]: (action) => ({
        event: 'subscription_created',
        ...action.payload,
    }),
    [api.billing.subscription.cancel.successType]: () => ({
        event: 'subscription_cancelled',
    }),
    [api.users.me.successType]: (action) => ({
        event: 'account_load',
        userId: action.result._id,
        email: action.result.email,
    }),
    [api.workspaces.retrieve.successType]: (action) => ({
        event: 'workspace_load',
        workspaceId: action.result._id,
        plan: action.result.billing.plan,
    }),
    [checkout.applyCoupon.type]: (action) => ({
        event: 'checkout.applyCoupon',
        coupon: action.payload.coupon,
    }),
    [checkout.removeCoupon.type]: () => ({
        event: 'checkout.removeCoupon',
    }),
    [checkout.changeCountry.type]: (action) => ({
        event: 'checkout.changeCountry',
        ...action.payload,
    }),
    [checkout.close.type]: () => ({
        event: 'checkout.close',
    }),
    [checkout.complete.type]: () => ({
        event: 'checkout.complete',
    }),
    [checkout.completePayment.type]: (action) => ({
        event: 'checkout.completePayment',
        ...action.payload,
    }),
    [checkout.load.type]: (action) => ({
        event: 'checkout.load',
        ...action.payload,
    }),
    [checkout.error.type]: (action) => ({
        event: 'checkout.error',
        ...action.payload,
    }),
    [checkout.selectPaymentMethod.type]: (action) => ({
        event: 'checkout.selectPaymentMethod',
        ...action.payload,
    }),
    [api.blocks.create.successType]: (action) => ({
        event: 'Create block',
        kind: action.result.kind,
    }),
    [api.blocks.update.successType]: (action) => ({
        event: 'Update block',
        kind: action.result.kind,
    }),
    [api.blocks.delete.successType]: (action, prevState) => {
        const website = prevState.websites.find(sift({ _id: action.websiteId }));
        const page = website?.pages.find(sift({ _id: action.pageId }));
        const block = page?.blocks.find(sift({ _id: action.blockId }));
        return {
            event: 'Delete block',
            kind: block?.kind,
        };
    },
    [api.blocks.deleteMany.successType]: (action, prevState) => {
        const website = prevState.websites.find(sift({ _id: action.websiteId }));
        const page = website?.pages.find(sift({ _id: action.pageId }));
        const blocks = page?.blocks.filter(sift({ _id: { $in: action.payload.blockId } })) || [];
        const uniqueKinds = [...new Set(blocks.map(({ kind }) => kind))];

        return {
            event: 'Delete many blocks',
            kinds: uniqueKinds,
        };
    },
    [api.websites.create.successType]: () => ({
        event: 'Create website',
    }),
    [api.websites.scaffold.successType]: () => ({
        event: 'Create website',
    }),
    [api.websites.delete.successType]: () => ({
        event: 'Delete website',
    }),
    [api.websites.domain.update.successType]: () => ({
        event: 'domain.connect',
    }),
    [api.websites.domain.delete.successType]: () => ({
        event: 'domain.disconnect',
    }),
    [api.products.create.successType]: () => ({
        event: 'products.create',
    }),
    [api.products.delete.successType]: () => ({
        event: 'products.delete',
    }),
    [api.products.update.successType]: () => ({
        event: 'products.update',
    }),
};

const gtm = GoogleTagManager();
const logger = process.env.NODE_ENV === 'development' ? require('@redux-beacon/logger').default : null;

export const gtmMiddleware = createMiddleware(eventsMap, gtm, { logger });
