import { createReducer, isMatchId, deleteIn } from '../../helpers';
import { api } from '../../actions';

const initialState = {
    integrations: [],
};

export default createReducer(initialState, {
    [api.integrations.create.success]: (state, action) => {
        state.integrations.push(action.result.integration);
    },
    [api.integrations.delete.success]: (state, action) => {
        deleteIn(state.integrations, isMatchId(action.integrationId));
    },
});
