import { createReducer, updateIn, deleteIn, isMatchId } from '../../helpers';
import { api } from '../../actions';

export default createReducer([], {
    [api.bots.create.success]: (state, action) => {
        state.push(action.result);
    },
    [api.bots.update.success]: (state, action) => {
        updateIn(state, isMatchId(action.botId), action.result);
    },
    [api.bots.delete.success]: (state, action) => {
        deleteIn(state, isMatchId(action.botId));
    },
});
