import { ProductsBlockKind, recursivelyVisitNestedBlocks } from '@mssgme/shared';
import { createReducer, deleteIn, isMatchId, updateIn } from '../helpers';
import { api } from '../actions';

const getWebsiteState = (state, action) => {
    return state.find(isMatchId(action.websiteId));
};

const getPagesState = (state, action) => {
    return getWebsiteState(state, action).pages;
};

const getPageState = (state, action) => {
    return getPagesState(state, action).find(isMatchId(action.pageId));
};

const getBlockById = (page, blockId) => {
    return page.blocks.find(isMatchId(blockId));
};

const unshiftResult = (state, action) => {
    state.unshift(action.result);
};

const acceptWebsite = (state, action) => {
    updateIn(state, isMatchId(action.websiteId), action.result);
};

const acceptBlocks = (state, action) => {
    getPageState(state, action).blocks = action.result;
};

const checkPublished = (state, action) => {
    const page = getPageState(state, action);

    if (!page.blocks.length) {
        const website = state.find(isMatchId(action.websiteId));

        page.enabled = false;
        website.enabled = false; // todo: add check for homePage here when multi-paging is supported
    }
};

export default createReducer([], {
    // Websites
    [api.websites.create.success]: unshiftResult,
    [api.websites.scaffold.success]: unshiftResult,
    [api.websites.list.success]: (state, action) => {
        return action.result;
    },
    [api.websites.update.success]: acceptWebsite,
    [api.websites.crm.settings.update.success]: acceptWebsite,
    [api.websites.delete.success]: (state, action) => {
        deleteIn(state, isMatchId(action.websiteId));
    },
    [api.websites.domain.update.success]: (state, action) => {
        state.find(isMatchId(action.websiteId)).domain = action.result;
    },
    [api.websites.domain.delete.success]: (state, action) => {
        state.find(isMatchId(action.websiteId)).domain = { name: '' };
    },

    // Uploads
    // [api.websites.upload.success]: (state, action) => {
    //     state.find(isMatchId(action.websiteId)).uploads.push(action.result);
    // },
    // [api.websites.uploadMany.success]: (state, action) => {
    //     state.find(isMatchId(action.websiteId)).uploads.push(...action.result.success.map(({ result }) => result));
    // },
    // [api.websites.deleteUpload.success]: (state, action) => {
    //     deleteIn(state.find(isMatchId(action.websiteId)).uploads, isMatchId(action.uploadId));
    // },

    // Pages
    [api.pages.create.success]: (state, action) => {
        getPagesState(state, action).unshift(action.result);
    },
    [api.pages.applyTemplate.success]: acceptWebsite,
    [api.pages.update.success]: (state, action) => {
        updateIn(getPagesState(state, action), isMatchId(action.pageId), action.result);
    },
    [api.pages.delete.success]: (state, action) => {
        deleteIn(getPagesState(state, action), isMatchId(action.pageId));
    },

    // Blocks
    [api.blocks.create.success]: (state, action) => {
        getPageState(state, action).blocks.push(action.result);
    },
    [api.blocks.toggle.success]: acceptBlocks,
    [api.blocks.group.success]: acceptBlocks,
    [api.blocks.clone.success]: acceptBlocks,
    [api.blocks.sort.success]: acceptBlocks,
    [api.blocks.update.success]: (state, action) => {
        updateIn(getPageState(state, action).blocks, isMatchId(action.blockId), action.result);
    },
    [api.blocks.convert.success]: (state, action) => {
        updateIn(state, isMatchId(action.websiteId), action.result.website);
    },
    [api.blocks.delete.success]: (state, action) => {
        const page = getPageState(state, action);
        const { results } = recursivelyVisitNestedBlocks(page, getBlockById(page, action.blockId), ({ _id }) => _id);

        page.blocks = page.blocks.filter(({ _id }) => !results.includes(_id));

        checkPublished(state, action);
    },
    [api.blocks.deleteMany.success]: (state, action) => {
        const page = getPageState(state, action);
        const allDeleted = [];

        for (const id of action.payload.blockId) {
            const { results } = recursivelyVisitNestedBlocks(page, getBlockById(page, id), ({ _id }) => _id);

            allDeleted.push(...results);
        }

        page.blocks = page.blocks.filter(({ _id }) => !allDeleted.includes(_id));

        checkPublished(state, action);
    },

    [api.collections.delete.success]: (state, action) => {
        const collectionId = action.collectionId;

        for (const { pages } of state) {
            for (const page of pages) {
                for (const { kind, collections } of page.blocks) {
                    if (kind !== ProductsBlockKind) {
                        continue;
                    }

                    if (collections?.include?.includes(collectionId)) {
                        collections.include = collections.include.filter((id) => id !== collectionId);
                    }
                }
            }
        }
    },
});
