// TODO: move into sagas
import store from 'store2';

const ALLOWED_REDIRECTS = ['com.mssgme.app://auth/cb']; // temporary solution

export function redirectAfterLogin() {
    const locationUrl = new URL(window.location.href);
    const redirectTo = locationUrl.searchParams.get('redirect_uri');
    if (ALLOWED_REDIRECTS.includes(redirectTo)) {
        window.location.replace(`${redirectTo}?access_token=${store.get('accessToken')}`);
    } else {
        window.location.replace('/pages');
    }
}

export function redirectAfterRegistration() {
    window.location.replace('/pages/create');
}
