import { createReducer, updateIn, deleteIn, isMatchId } from '../../helpers';

import { api } from '../../actions';

const initialState = {
    replies: [],
};

export default createReducer(initialState, {
    [api.replies.list.success]: (state, action) => {
        state.replies = action.result;
    },
    [api.replies.create.success]: (state, action) => {
        state.replies.unshift(action.result);
    },
    [api.replies.update.success]: (state, action) => {
        updateIn(state.replies, isMatchId(action.replyId), action.result);
    },
    [api.replies.delete.success]: (state, action) => {
        deleteIn(state.replies, isMatchId(action.replyId));
    },
});
