import { createReducer } from '../helpers';
import { api } from '../actions';

const setIsFetching = (bool) => (state) => {
    state.isFetching = bool;
};

const initialState = {
    items: [],
    hasNext: true,
    next: null,
    filter: {},
    isFetching: false,
    guid: 0,
};

export default createReducer(initialState, {
    [api.websites.audience.list.request]: setIsFetching(true),
    [api.websites.audience.list.failure]: setIsFetching(false),
    [api.websites.audience.list.success]: (state, { guid, result, query }) => {
        if (guid && guid < state.guid) {
            return state;
        }

        state.hasNext = result.hasNext;
        state.next = result.next;
        state.isFetching = false;

        if (query?.next) {
            state.items.push(...result.results);
        } else {
            state.items = result.results;
        }
    },

    SET_AUDIENCE_FILTER: (state, action) => {
        state.filter = action.payload || {};
        state.guid = state.guid + 1;
        state.next = null;
    },
    CLEAR_AUDIENCE_STORE: () => initialState,
});
