import React from 'react';
import { useParams, useLocation } from 'react-router-dom';

import { queryString } from '@mssgme/shared';
import AuthByCodePage from '../AuthByCode';

export default function LoginCodeAuthPage() {
    const { code } = useParams();
    const { search } = useLocation();
    const { redirect_to, back_track } = queryString.parse(search);

    return <AuthByCodePage code={code} redirectTo={redirect_to} backTrack={back_track} />;
}
